// Color Pallete

$white: #fff;
$light: #f8f9fa;
$dark: #343a40;
$black: #000;

$blue: #007bff;
$blue-light: #007bff20;
$blue-hover: #007bff30;
$blue-light-opaque: #eff7ff;
$blue-mid: #007bff99;
$pblue: #4c75f2;
$pblue-light: #4c75f210;
$pblue-hover: #4c75f230;
$pblue-light-opaque: #eff7ff;
$pblue-mid: #4c75f299;
$pblue-dark: #4c75f2dd;
$cherry: #ff073a;
$cherry-mid: #ff073a99;
$cherry-light: #ff073a20;
$cherry-hover: #ff073a30;
$cherry-light-opaque: #ffe0e6;
$red: #dc3545;
$red-light: #dc354520;
$red-mid: #dc354599;
$red-hover: #dc354530;
$orange: #fd7e14;
$orange-mid: #fd7e1499;
$orange-light: #fd7e1420;
$orange-hover: #fd7e1430;
$orange-light-opaque: #ffefe2;
$yellow: #ffc107;
$yellow-light: #ffc10720;
$yellow-hover: #ffc10730;
$yellow-mid: #ffc10799;
$yellow-light-opaque: #fff7e0;
$green: #28a745;
$green-light: #28a74520;
$green-hover: #28a74530;
$green-mid: #28a74599;
$gray: #6c757d;
$gray-light: #6c757d10;
$gray-light-opaque: #f6f6f7;
$gray-hover: #6c757d20;
$gray-mid: #6c757d99;
$gray-middark: #6c757d30;
$gray-dark: #343a40;
$gray-opaque: #f1f1f1;
$gray-hover-opaque: #edeeef;
$gray-hover-darker-opaque: #f6f6f7;
$purple: #201aa2dd;
$purple-light: #201aa220;
$purple-hover: #201aa230;
$purple-mid: #201aa299;
$purple-light-opaque: #e3e2f3;
$pink: #db5581;
$pink-light: #db558120;
$pink-hover: #db558130;
$pink-mid: #db558199;
$brown: #b6854d;
$brown-light: #b6854d20;
$brown-hover: #b6854d30;
$brown-mid: #b6854d99;
$brick: #e23028;
$brick-light: #e2302810;
$brick-hover: #e2302820;

// Dark mode variables
$dark-m-gray: #161625;
$dark-mid-subtext: #bdb8ae;
$dark-m-white: #e1e1e1;
$dark-m-blue: #3391ff;
$dark-m-purple: #9673b9;
$dark-m-purple-mid: #9673b9bb;
$dark-m-purple-light: #40008050;
$dark-m-purple-hover: #40008090;

* {
  font-family: 'archia';
}

html {
  overflow-x: hidden;
  width: 100vw;
}

body {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 24px !important;
  font-weight: 900;
}

h2 {
  font-size: 18px !important;
  font-weight: 400;
}

h3 {
  font-size: 14px !important;
  font-weight: 400;
}

h4 {
  font-size: 13px !important;
  font-weight: 900;
}

h5 {
  font-size: 12px !important;
  font-weight: 900;
}

h6 {
  font-size: 11px !important;
  font-weight: 600;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.arrow-up {
  border-bottom: 5px solid $gray-mid;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  margin-left: 0.25rem;
}

.arrow-down {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid $gray-mid;
  margin-left: 0.25rem;
}

.Navbar {
  align-items: center;
  display: flex;
  flex-direction: row;
  height: 4rem;
  justify-content: space-between;

  a {
    color: inherit;
    text-decoration: none;
  }

  .navbar-left {
    color: $gray-mid;
    cursor: pointer;
    font-size: 12px;
    font-weight: 600;
    padding: 1.5rem;
    text-align: center;
    width: 3rem;

    &:hover {
      color: $gray;
    }
  }

  .navbar-middle {
    color: $gray;
    font-size: 18px;
    font-weight: 900;
    text-transform: uppercase;

    span {
      color: $pblue;
    }
  }

  .navbar-right {
    color: $gray;
    cursor: pointer;
    font-size: 12px;
    font-weight: 600;
    padding: 1.5rem;
    text-align: center;
    width: 3rem;
  }

  .language-toggle {
    cursor: pointer;
    font-weight: 900;
    text-transform: uppercase;
  }

  .expand {
    background: $gray-light-opaque;
    color: $gray;
    display: flex;
    flex-direction: column;
    width: 100vw;
    z-index: 9999;

    & > * {
      border-bottom: 1px solid $gray-light;
      border-top: 1px solid $gray-light;
      padding: 1.5rem;
      transition: all 0.15s ease-in-out;

      &:hover {
        background: $pblue-hover;
      }

      span {
        &.focused {
          background: $pblue-light;
          color: $pblue;
          padding: 0.25rem;
        }
      }
    }

    .expand-bottom {
      align-self: flex-start;
      border: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;

      h5 {
        margin: 0;
      }

      &:hover {
        background: none;
      }
    }
  }
}

.Home {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: 9rem;
  margin-right: 3rem;

  .home-left,
  .home-right {
    display: flex;
    flex-direction: column;
  }

  .home-left {
    margin-right: 2.5rem;
  }

  .home-right {
    margin-left: 2.5rem;
  }

  .header {
    z-index: 99;
  }
}

.alert {
  align-items: flex-start;
  background: $gray-light;
  border-radius: 5px;
  color: $gray;
  display: flex;
  flex-direction: row;
  font-size: 10px;
  padding: 0.5rem;

  svg {
    color: $gray-mid;
    height: 16px;
    width: 16px;
  }

  .alert-right {
    margin-left: 0.25rem;
    text-align: right;
    width: 8.25rem;

    a {
      background: $gray-light;
      color: $gray;
      padding-left: 0.1rem;
      padding-right: 0.1rem;

      &:hover {
        background: $gray-hover;
      }
    }

    &.is-full {
      width: 100%;
    }
  }

  &.is-green {
    background: $green-light;
    color: $green;

    svg {
      stroke: $green-mid;
    }
  }
}

.StateHeader {
  align-self: center;
  display: flex;
  flex-direction: row;
  height: 5.25rem;
  justify-content: space-between;
  margin-bottom: 1rem;
  position: relative;
  width: 100%;

  & > * {
    align-self: flex-start;
  }

  .header-left {
    h1,
    h5 {
      margin: 0;
    }

    h1 {
      color: $brick;
      max-width: 20rem;
    }

    h5 {
      color: $gray;
      font-weight: 600;
      margin-top: 0.5rem;
    }
  }

  .header-right {
    color: $purple-mid;
    display: flex;
    flex-direction: column;
    text-align: right;

    h2,
    h5 {
      font-weight: 600;
      margin: 0;
    }

    h2 {
      color: $purple;
      font-weight: 900;
    }

    a {
      background: $purple-light;
      color: inherit;
      text-decoration: none;

      &:hover {
        background: $purple-hover;
      }
    }
  }
}

.State {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: 9rem;
  margin-right: 3rem;

  .state-left,
  .state-right {
    display: flex;
    flex-direction: column;
  }

  .state-left {
    margin-right: 2.5rem;
    position: relative;
  }

  .state-right {
    margin-left: 2.5rem;
  }

  a {
    text-decoration: none;
  }

  .district-bar {
    margin-bottom: 2.5rem;
    width: calc(100% - 2rem);
  }

  .MapExplorer {
    .header,
    .map-stats,
    .map-button,
    .tabs-map,
    .last-update,
    .footnote {
      display: none;
    }

    .anchor {
      display: none;
    }
  }

  .TimeseriesExplorer {
    .anchor {
      display: none;
    }
  }
}

.StateMeta {
  align-self: center;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 3rem;
  width: calc(100% - 3rem);

  &.population {
    display: flex !important;
    flex-direction: row !important;
    justify-content: space-between !important;
    margin: 0;
    margin-bottom: 2.5rem;
    margin-top: 2.5rem;

    .alert {
      margin: 0;
    }
  }

  .meta-item {
    border-radius: 5px;
    min-height: 6rem;
    padding-left: 1rem;
    padding-top: 1rem;

    h1,
    h2,
    h3,
    h4,
    h5 {
      margin: 0;
    }

    h5 {
      font-weight: 600;
    }

    h2 {
      font-weight: 600;
    }

    h3 {
      font-weight: 600;
      width: 10rem;
      word-wrap: break-word;
    }

    h1 {
      display: inline-block;
      font-weight: 600;
    }

    &.population {
      min-height: 1rem !important;
      padding: 0 !important;

      h1,
      h3 {
        color: $gray;
      }
    }

    .meta-item-top {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-right: 1rem;

      & > * {
        align-self: center;
      }

      h3 {
        width: calc(100% - 2.5rem);
      }

      svg {
        align-self: flex-start;
        cursor: pointer;
        stroke-width: 2.5px;
        width: 16px;
      }
    }

    .meta-item-middle {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      * {
        align-self: center;
      }

      svg {
        cursor: pointer;
        margin-right: 1rem;
      }
    }

    p {
      font-weight: 600;
      margin-bottom: 1.25rem;
      padding-right: 1rem;
    }

    &.confirmed {
      background: $cherry-light;

      h3,
      h5,
      svg,
      p {
        color: $cherry-mid;
      }

      h1 {
        color: $cherry;
      }
    }

    &.active {
      background: $blue-light;

      h3,
      h5,
      svg,
      p {
        color: $blue-mid;
      }

      h1 {
        color: $blue;
      }
    }

    &.recovery {
      background: $green-light;

      h3,
      h5,
      svg,
      p {
        color: $green-mid;
      }

      h1 {
        color: $green;
      }
    }

    &.mortality {
      background: $gray-light;

      h3,
      h5,
      svg,
      p {
        color: $gray-mid;
      }

      h1 {
        color: $gray;
      }
    }

    &.cpl {
      background: $yellow-light;

      h3,
      h5,
      svg {
        color: $orange-mid;
      }

      h1 {
        color: $orange;
      }
    }

    &.tpl {
      background: $purple-light;

      h3,
      h5,
      svg,
      p {
        color: $purple-mid;
      }

      h1 {
        color: $purple;
      }
    }

    &.tpr {
      background: $orange-light;

      h3,
      h5,
      p,
      svg {
        color: $orange-mid;
      }

      h1 {
        color: $orange;
      }
    }
  }
}

.StateDropdown {
  align-items: center;
  display: flex;
  flex-direction: row;
  position: relative;

  .state-name {
    background: $brick-light;
    border-radius: 5px;
    cursor: pointer;
    padding: 0.25rem;

    &:hover {
      &:not(.expanded) {
        background-color: $brick-hover;
      }
    }
  }

  .dropdown {
    background: $white;
    border-radius: 5px;
    height: 25rem;
    left: -0.25rem;
    overflow-y: scroll;
    position: absolute;
    top: 2.75rem;
    width: 15rem;
    z-index: 999;

    .item {
      color: $red;
      cursor: pointer;
      padding: 0.75rem;

      &:hover {
        background: $red-hover;
      }
    }
  }

  .backdrop {
    background: $red-light;
    height: 100vh;
    left: 0;
    pointer-events: none;
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 10;
  }
}

.StatisticDropdown {
  margin-left: -3px;
  margin-top: 0.5rem;
  position: relative;

  .triangle-icon {
    left: -16px;
    position: absolute;
    top: 2px;

    svg {
      vertical-align: middle !important;
    }
  }

  select {
    appearance: none;
    background-color: $gray-light;
    border: 0;
    border-radius: 3px;
    color: $gray;
    cursor: pointer;
    font-size: 12px;
    font-weight: 600;
    max-width: 14rem;
    min-width: 3rem;
    outline-color: $gray-hover;
    padding: 3px;

    option {
      background-color: #eee;
      color: $gray;
      padding: 3px;
    }

    &:hover {
      background-color: $gray-hover;
    }

    &.confirmed {
      background-color: $cherry-light;
      color: $cherry;
      outline-color: $cherry-hover;

      &:hover {
        background-color: $cherry-hover;
      }
    }

    &.active {
      background-color: $blue-light;
      color: $blue;
      outline-color: $blue-hover;

      &:hover {
        background-color: $blue-hover;
      }
    }

    &.recovered {
      background-color: $green-light;
      color: $green;
      outline-color: $green-hover;

      &:hover {
        background-color: $green-hover;
      }
    }

    &.tested {
      background-color: $purple-light;
      color: $purple;
      outline-color: $purple-hover;

      &:hover {
        background-color: $purple-hover;
      }
    }

    &.other,
    &.tpr,
    &.cfr {
      background-color: $orange-light;
      color: $orange;
      outline-color: $orange-hover;

      &:hover {
        background-color: $orange-hover;
      }
    }

    &.vaccinated,
    &.vaccinated1,
    &.vaccinated2 {
      background-color: $pink-light;
      color: $pink;
      outline-color: $pink-hover;

      &:hover {
        background-color: $pink-hover;
      }
    }

    &.population {
      background-color: $brown-light;
      color: $brown;
      outline-color: $brown-hover;

      &:hover {
        background-color: $brown-hover;
      }
    }
  }
}

.labels {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-bottom: 1rem;

  .label {
    align-items: center;
    background: $gray-light !important;
    border-radius: 5px !important;
    color: $gray-mid !important;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    font-size: 14px;
    margin-bottom: 0.25rem;
    margin-right: 0.25rem;
    padding: 0.25rem 0.5rem;
    text-transform: capitalize;

    &.is-selected {
      background: $yellow-hover !important;
      color: $orange-mid !important;

      &:hover {
        background: $yellow-hover !important;
        color: $orange-mid !important;
      }
    }

    @media not all and (pointer: coarse) {
      &:hover {
        background: $yellow-light !important;
        color: $orange-mid !important;
      }
    }

    .count {
      align-items: center;
      background: inherit;
      border-radius: 50%;
      color: inherit;
      display: flex;
      flex-direction: row;
      font-size: 12px;
      height: 1.5rem;
      justify-content: center;
      margin-left: 0.25rem;
      width: 1.5rem;
    }
  }
}

.Banner {
  background: $red-light;
  color: $red;
  display: flex;
  flex-direction: row;
  font-size: 12px;
  font-weight: 600;
  justify-content: center;
  text-decoration: none;

  .wrapper {
    align-items: center;
    align-self: center;
    display: flex;
    justify-content: center;
    padding: 1rem 0.75rem;
    padding-left: 7rem;
    text-align: center;
    word-wrap: break-word;

    .content {
      margin: 0 0.5rem;
    }

    a {
      align-items: center;
      background-color: $red-light;
      color: $red;
      display: flex;
      padding: 1px;
      text-decoration: none;
      white-space: nowrap;

      &:hover {
        background-color: $red-hover;
      }
    }
  }
}

.Search {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  position: relative;
  width: 100%;

  & > * {
    align-self: center;
  }

  label {
    color: $gray;
    font-size: 0.75rem;
    margin-bottom: 1rem;
  }

  .line {
    background: $gray-light;
    height: 1rem;
    width: 2px;
  }

  input {
    appearance: none !important;
    background: #fff;
    border: 0;
    border-radius: 5px;
    box-shadow: 0 10px 35px rgba(0, 0, 0, 0.1);
    color: $gray;
    font-size: 1rem;
    height: 2rem;
    outline: none;
    padding: 1rem;
    padding-left: 3rem;
    width: calc(100% - 4rem);
  }

  .search-input-wrapper {
    position: relative;
    width: 100%;
  }

  .search-placeholder {
    color: $gray-mid;
    font-size: 1rem;
    left: 3rem;
    opacity: 0.8;
    pointer-events: none;
    position: absolute;
    top: 1.4rem;
    transition: opacity 1s;

    &.disappear {
      opacity: 0;
    }
  }

  .search-button {
    svg {
      color: $gray-mid;
      left: 0.75rem;
      position: absolute;
      top: 1.25rem;
      transition: all 0.2s ease-in-out;
    }

    &.is-expand {
      svg {
        left: 0.5rem;
      }
    }
  }

  .close-button {
    background: $gray-light !important;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    height: 20px;
    justify-content: center;
    position: absolute;
    right: 1rem;
    top: 1.35rem;
    transition: all 0.2s ease-in-out;
    width: 20px;

    &:hover {
      background: $gray-hover !important;
    }

    &.is-expand {
      right: 0.75rem;
    }

    svg {
      align-self: center;
      position: absolute;
      stroke: $gray-mid;
      stroke-width: 3;
      width: 12px;
    }

    &.custom {
      background: $pink-light !important;
      pointer-events: none;
      z-index: 0;

      svg {
        stroke: $pink !important;
      }
    }
  }

  .feature {
    background: $pink-light;
    border-radius: 5px;
    color: $pink;
    cursor: pointer;
    font-size: 0.75rem;
    padding: 0.75rem;
    text-align: center;
  }

  .expanded {
    display: flex;
    flex-direction: row;
    margin-top: 2rem;
    padding: 1rem;
    padding-bottom: 0;
    width: calc(100% - 4rem);

    & > * {
      flex: 1;
    }

    h4,
    h3 {
      color: $gray;
      font-weight: 600;
      margin: 0;
      margin-bottom: 0.5rem;
    }

    h3 {
      color: $pblue;
      margin-bottom: 0.75rem;
    }
  }

  .results {
    padding: 1rem;
    padding-bottom: 0;
    width: calc(100% - 2rem);

    a {
      text-decoration: none !important;
    }

    .pan-divider {
      color: $gray;
      font-weight: 600;
      padding: 1rem 0;
    }

    .result,
    .essential-result {
      align-self: flex-start;
      border-bottom: 2px solid $gray-light;
      color: $gray;
      cursor: pointer;
      display: flex;
      flex-direction: row;
      font-size: 0.9rem;
      justify-content: space-between;
      min-height: 2rem;
      padding: 1rem;
      transition: all 0.2s ease-in-out;

      &:hover {
        background: $gray-light;
      }

      & > * {
        align-self: center;
      }

      .result-name {
        max-width: 15rem;
      }

      .result-type {
        background: $yellow-light;
        border-radius: 2px;
        color: $orange;
        display: flex;
        flex-direction: row;
        font-size: 0.75rem;
        padding: 0.25rem;

        &:hover {
          background: $yellow-hover;
        }

        svg {
          margin-left: 0.25rem;
          stroke: $orange-mid;
          stroke-width: 3px;
        }

        * {
          align-self: center;
        }
      }

      .result-left {
        display: flex;
        flex-direction: row;

        * {
          align-self: center;
        }
      }

      .result-zone {
        border-radius: 50%;
        height: 0.75rem;
        margin-left: 0.5rem;
        width: 0.75rem;

        &.is-red {
          background: $cherry-light;
          border: 2px solid $cherry-mid;
        }

        &.is-orange {
          background: $orange-light;
          border: 2px solid $orange-mid;
        }

        &.is-green {
          background: $green-light;
          border: 2px solid $green-mid;
        }
      }
    }

    .essential-result {
      display: flex;
      flex-direction: column;

      .result-top {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;

        .result-top-left {
          display: flex;
          flex-direction: column;

          .result-location {
            display: flex;
            flex-direction: row;
            font-size: 0.75rem;
          }

          .result-distance {
            color: $green;
            display: flex;
            flex-direction: row;
            font-size: 0.75rem;
          }
        }
      }

      .result-category {
        align-self: flex-start;
        background: $pink-light;
        border-radius: 2px;
        color: $pink;
        display: flex;
        flex-direction: row;
        font-size: 0.75rem;
        height: 1rem;
        padding: 0.25rem;
        text-align: right;
        text-transform: capitalize;

        &:hover {
          background: $pink-hover;
        }

        & > * {
          align-self: center;
        }

        svg {
          margin-left: 0.25rem;
          stroke-width: 3px;
          width: 12px;
        }
      }

      .result-description {
        align-self: flex-start;
        font-size: 0.75rem;
        margin-bottom: 0.5rem;
        margin-top: 1rem;
      }

      .result-contacts {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
      }

      .result-contact {
        align-self: flex-start;
        background: $gray-light;
        border-radius: 2px;
        color: $gray;
        display: flex;
        flex-direction: row;
        font-size: 0.75rem;
        height: 1rem;
        margin-right: 0.25rem;
        margin-top: 0.25rem;
        padding: 0.25rem;
        text-align: right;

        &:hover {
          background: $gray-hover;
        }

        a {
          color: inherit;
        }

        & > * {
          align-self: center;
        }

        svg {
          margin-right: 0.25rem;
          stroke: $gray-mid;
          stroke-width: 3px;
          width: 12px;
        }
      }
    }
  }

  .suggestions {
    display: flex;
    flex-direction: column;
    margin-right: 1rem;

    .suggestion {
      color: $gray-mid;
      display: flex;
      flex-direction: row;
      padding: 0.1rem;

      div {
        padding-top: 1px;
      }

      h4 {
        cursor: pointer;
        margin-left: 0.25rem;
        word-wrap: wrap;

        &:hover {
          text-decoration: underline;
          text-decoration-thickness: 2px;
        }
      }
    }
  }
}

.header {
  align-self: center;
  display: flex;
  flex-direction: column;
  max-width: 40rem;
  width: 100%;

  & > * {
    align-self: center;
  }
}

.ActionsPanel {
  margin-bottom: 0.3rem;
  padding-bottom: 1rem;
  padding-top: 1rem;
  position: relative;
  width: calc(100% - 1rem);

  .actions {
    display: flex;
    flex-direction: row;
    justify-content: center;
    transition: all 1s;
    width: 100%;

    & > * {
      align-self: center;
      margin-left: 0.5rem;
      margin-right: 0.5rem;
    }

    h5 {
      color: $gray;
      font-weight: 600;
      margin: 0;
    }

    svg {
      cursor: pointer;
      fill: #a6abb0;
      stroke: #a6abb0;
      stroke-width: 0.5;
      user-select: none;
      width: 16px;

      &:hover {
        fill: $gray;
        stroke: $gray;
      }
    }

    .timeline-icon {
      display: flex;
      flex-direction: column;
      justify-content: center;

      svg {
        display: flex;
        flex-direction: row;
        justify-content: center;

        path {
          align-self: center;
        }
      }
    }

    .bell-icon {
      height: 24px;
      position: relative;

      .indicator {
        background: $pblue;
        border-radius: 50%;
        height: 7px;
        position: absolute;
        right: -3px;
        top: -3px;
        width: 7px;
      }
    }
  }
}

.Timeline {
  align-items: center;
  user-select: none;
  width: 100%;

  .timeline {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    position: absolute;
    top: 0;

    .highlight {
      color: $brown !important;
      opacity: 0;
      position: absolute;
      text-align: center;
      top: 3.25rem;
      transform: translate3d(0, 10px, 0);
      transition: all 0.3s;
      width: 100%;

      &.current {
        opacity: 1;
        transform: translate3d(0, 0, 0);
      }

      svg {
        fill: #dc3545dd;
        pointer-events: none;
        stroke: none;
      }
    }

    .wheel {
      align-items: center;
      color: #fff;
      display: block;
      height: 3rem;
      margin-top: -0.75rem;
      overflow: hidden;
      width: 100%;
    }

    .wheel__inner {
      align-items: center;
      display: flex;
      height: 100%;
      justify-content: center;
      pointer-events: none;
      transform-style: preserve-3d;
      width: 100%;
    }

    .wheel__slides {
      height: 100%;
      margin-top: 2px;
      position: relative;
      width: 100%;
    }

    .wheel__slide {
      -webkit-backface-visibility: hidden;
      align-items: center;
      backface-visibility: hidden;
      display: flex;
      height: 100%;
      justify-content: center;
      position: absolute;
      width: 100%;

      h5 {
        -moz-user-select: -moz-none;
        -webkit-user-select: none;
        border-radius: 4px;
        cursor: pointer;
        opacity: 0.6;
        padding: 4px;
        pointer-events: all;
        transition: all 0.3s;
        user-select: none;

        &.current {
          background-color: #6c757d20;
          opacity: 0.8;

          &:hover {
            opacity: 1;
          }
        }

        &:hover {
          background-color: #6c757d30;
          opacity: 0.8;
        }

        &::selection {
          background: transparent !important;
        }
      }

      .calendar-icon {
        align-self: center;
        cursor: pointer;
        display: flex;
        margin-left: 0.2rem;
        opacity: 0;
        transition: opacity 0.15s ease-in-out;

        &.show {
          opacity: 1;
          pointer-events: all;
        }
      }
    }

    .wheel-buttons {
      display: flex;
      justify-content: space-evenly;
      width: 100%;
      z-index: 1;

      .wheel-button {
        align-items: center;
        color: $gray;
        display: flex;

        &.left {
          svg {
            transform: rotate(180deg);
          }
        }

        &.active {
          svg {
            fill: $pblue;
            stroke: none;

            &:hover {
              fill: $pblue-mid;
              stroke: none;
            }
          }
        }
      }
    }
  }

  .Calendar {
    align-items: center;
    display: flex;
    justify-content: center;

    .react-calendar {
      border-radius: 5px;
      line-height: 2em;

      *,
      *::before,
      *::after {
        -moz-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
      }

      .react-calendar__navigation {
        margin-bottom: 1em;
      }

      button {
        border: 0;
        border-radius: 5px;
        margin: 0.125rem;
        outline: none;

        &:enabled {
          &:hover {
            cursor: pointer;
          }
        }
      }
    }

    .react-calendar__viewContainer {
      display: flex;
    }

    .react-calendar__navigation {
      font-size: 25px;

      button {
        background-color: $gray-light;
        color: $gray;
        min-width: 44px;
        padding: 1em 0.5em;

        &:enabled {
          &:hover {
            background-color: #6c757d40;
          }
        }

        &:disabled {
          background: none;
        }

        div {
          align-items: center;
          display: flex;
          justify-content: center;
        }
      }
    }

    .react-calendar__month-view__weekdays {
      font-size: 0.75em;
      font-weight: 600;
      text-align: center;
      text-transform: uppercase;
    }

    .react-calendar__month-view__weekdays__weekday {
      padding: 0.5em;
    }

    .react-calendar__tile {
      background: none;
      color: $gray;
      max-width: 100%;
      padding: 1em 0.5em;
      text-align: center;

      &:enabled {
        background-color: $gray-light;

        &.react-calendar__tile--now {
          background-color: #ffff76;
          color: $black;

          &:hover {
            background-color: #ffffa9;
          }
        }

        &.react-calendar__tile--active,
        &.react-calendar__tile--hasActive {
          background-color: #006edc;
          color: $white;

          &:hover {
            background-color: #1087ff;
          }
        }

        &.react-calendar__month-view__days__day--neighboringMonth {
          background-color: #6c757d06;
          color: #6c757d99;
        }

        &:hover {
          background-color: $gray-hover;
        }
      }
    }

    .react-calendar__month-view {
      .react-calendar__tile {
        flex-basis: calc(100% / 7 - 0.25rem) !important;
        max-width: calc(100% / 7 - 0.25rem) !important;
      }
    }

    .react-calendar__year-view {
      .react-calendar__tile {
        flex-basis: calc(100% / 3 - 0.25rem) !important;
        max-width: calc(100% / 3 - 0.25rem) !important;
      }
    }

    .react-calendar__month-view__days__day--weekend {
      color: $cherry;
    }

    .react-calendar__year-view,
    .react-calendar__decade-view,
    .react-calendar__century-view {
      .react-calendar__tile {
        color: $gray;
        padding: 2em 0.5em;
      }
    }
  }
}

.button {
  background: $blue-light;
  border: 0;
  border-radius: 5px;
  color: $blue-mid;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  font-size: 0.75rem;
  font-weight: 900;
  margin-bottom: 0.5rem;
  margin-top: 0.25rem;
  outline: none;
  padding: 0.5rem;
  text-decoration: none;
  text-transform: uppercase;
  transition: background 0.2s ease-in-out;

  svg {
    padding-right: 6px;
    stroke-width: 2px;
    width: 20px;
  }

  &.is-purple {
    background: $purple-light;
    color: $purple-mid;

    &:hover {
      background: $purple-hover;
    }
  }

  &.is-green {
    background: $green-light;
    color: $green-mid;

    &:hover {
      background: $green-hover;
    }
  }

  &:hover {
    background: $blue-hover;
  }

  & > * {
    align-self: center;
  }
}

.MapSwitcher {
  align-self: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: absolute;
  width: 100%;

  .highlight {
    pointer-events: none;
    position: absolute;
  }

  .highlight,
  .clickable {
    border-radius: 5px;
    cursor: pointer;
    height: 14rem;
    width: calc(25%);
    z-index: 10;
  }

  .clickable {
    transition: background 0.15s ease-in-out;

    &:hover {
      &.is-confirmed {
        background: $cherry-light;
      }

      &.is-active {
        background: $blue-light;
      }

      &.is-recovered {
        background: $green-light;
      }

      &.is-deceased {
        background: $gray-light;
      }
    }
  }
}

.Level {
  align-self: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;

  .level-item {
    display: flex;
    flex-direction: column;

    & > * {
      align-self: center;
    }
  }

  h1,
  h5 {
    margin-bottom: 0;
  }

  h1 {
    font-size: min(3.75vw, 22px) !important;
    font-weight: 600;
  }

  h4 {
    margin-bottom: -0.5rem;
  }

  h5 {
    font-weight: 600;
    overflow-wrap: break-word;
    text-align: center;
    width: 100%;
  }

  .is-confirmed {
    h1,
    h5 {
      color: $cherry;
    }

    h4 {
      color: $cherry-mid;
    }
  }

  .is-active {
    h1,
    h5 {
      color: $blue;
    }

    h4 {
      color: $blue-mid;
    }
  }

  .is-recovered {
    h1,
    h5 {
      color: $green;
    }

    h4 {
      color: $green-mid;
    }
  }

  .is-deceased {
    h1,
    h5 {
      color: $gray;
    }

    h4 {
      color: $gray-mid;
    }
  }

  .is-tested {
    h1,
    h5 {
      color: $purple;
    }

    h4 {
      color: $purple-mid;
    }
  }

  .is-vaccinated {
    h1,
    h5 {
      color: $pink;
    }

    h4 {
      color: $pink-mid;
    }
  }

  .is-tpr {
    h1,
    h5 {
      color: $orange;
    }

    h4 {
      color: $orange-mid;
    }
  }
}

.VaccinationHeader {
  align-items: center;
  align-self: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 0.25rem;
  margin-top: 2rem;
  width: 100%;

  .level-vaccinated {
    align-items: center;
    align-self: center;
    animation-delay: 200ms;
    background: $pink-light;
    border-radius: 5px;
    color: $pink;
    display: flex;
    flex-direction: row;
    margin-bottom: 0.5rem;
    padding: 0.5rem;

    div {
      font-size: 13px;
      font-weight: 500;
      margin: 0 0 0 0.25rem;
    }
  }

  .progress-bar-wrapper {
    color: $gray;
    font-size: 12px;
    margin-top: 0.5rem;
    position: relative;
    width: 100%;

    .progress-bar {
      background-color: $pink-light;
      border-radius: 5px;
      color: $pink;
      height: 24px;
      margin-bottom: 0.1rem;
      margin-top: 0.1rem;
      position: relative;

      &.value {
        background-image: linear-gradient(
          45deg,
          $pink-mid 25%,
          $pink-light 25%,
          $pink-light 50%,
          $pink-mid 50%,
          $pink-mid 75%,
          $pink-light 75%,
          $pink-light 100%
        );
        background-size: 14px 14px;
        cursor: pointer;
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: center;
        margin: 0;
        opacity: 0.6;
        position: absolute;

        &.opaque {
          background: $pink-mid;
        }

        &.highlighted {
          opacity: 1;
        }

        span {
          margin-left: calc(100% + 0.25rem);
          width: max-content;
        }
      }
    }

    .legend {
      opacity: 0.6;

      &.highlighted {
        opacity: 1;
      }

      .label-wrapper {
        display: flex;

        .label {
          cursor: pointer;
          flex-shrink: 0;
        }
      }

      .arrow {
        cursor: pointer;
        margin-bottom: 0.2rem;
        width: fit-content;
      }
    }
  }
}

abbr {
  text-decoration: none;

  &.is-cherry {
    color: $cherry;
  }

  &.is-blue {
    color: $blue;
  }

  &.is-green {
    color: $green;
  }

  &.is-gray {
    color: $gray;
  }
}

.TableLoader {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}

.Icons {
  font-size: 12px;
  font-weight: 600;

  span {
    font-size: 8px;
    font-weight: 600;
    line-height: 6px;
    position: absolute;
    right: 5px;
    top: 5px;
    user-select: none;
  }
}

.table-top {
  align-items: flex-end;
  align-self: center;
  display: flex;
  flex-direction: row;
  font-weight: 600;
  justify-content: space-between;
  margin-bottom: 0.25rem;
  width: 100%;

  .table-top-left {
    display: flex;
    flex-direction: row;
  }

  .toggle {
    align-items: center;
    background: $gray-light;
    border-radius: 5px;
    color: $gray-mid;
    cursor: pointer;
    display: flex;
    height: 2.5rem;
    justify-content: center;
    margin-right: 0.25rem;
    user-select: none;
    width: 2.5rem;

    &.disabled {
      cursor: not-allowed;
    }

    svg {
      margin: 0.25rem;
    }

    &:hover {
      &:not(.disabled) {
        background: $gray-hover;
      }
    }
  }

  .option-toggle,
  .expand-table-toggle {
    &.is-highlighted {
      background: $green-light;
      color: $green;

      &:hover {
        background: $green-hover;
      }
    }
  }

  .info-toggle {
    &.is-highlighted {
      background: $yellow-light;
      color: $yellow;

      &:hover {
        background: $yellow-hover;
      }
    }
  }

  .lakh-toggle {
    &.is-highlighted {
      background: $pink-light;
      color: $pink;

      &:hover {
        background: $pink-hover;
      }
    }
  }

  .delta-toggle {
    &.is-highlighted {
      background: $blue-light;
      color: $blue;

      &:hover {
        background: $blue-hover;
      }
    }
  }

  .expand-table-toggle {
    color: $gray-mid;
    margin-left: auto;
    margin-right: 0;

    &.is-highlighted {
      svg {
        transform: rotate(90deg);
      }
    }

    svg {
      cursor: pointer;
      margin-bottom: 0.25rem;
      transform: rotate(-90deg);
      transition: transform 0.15s ease-in-out;
    }
  }

  @media (min-width: 769px) {
    margin-top: 1rem;
  }
}

.table-helper {
  align-self: center;
  color: $gray;
  display: flex;
  flex-direction: column;
  width: calc(100% - 2rem);

  h5 {
    color: $gray-mid;
  }

  .helper-top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .info-item {
    align-items: center;
    color: $gray;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    font-weight: 600;
    height: 1.5rem;

    &:first-child {
      margin-top: 1rem;
    }

    :first-child {
      width: 1.6rem;

      svg {
        width: 15px;
      }
    }

    p {
      max-width: calc(100% - 1.6rem);
    }

    svg {
      color: $gray-mid;
      margin-bottom: 0.25rem;
    }

    .is-confirmed {
      svg {
        color: $cherry;
      }
    }

    .is-active {
      svg {
        color: $blue;
      }
    }

    .is-recovered {
      svg {
        color: $green;
      }
    }

    .is-deceased {
      svg {
        color: $gray;
      }
    }

    .is-tested {
      svg {
        color: $purple;
      }
    }

    .is-vaccinated,
    .is-vaccinated1,
    .is-vaccinated2 {
      svg {
        color: $pink;
      }
    }

    &.notes {
      svg {
        margin-bottom: 0;
      }
    }

    &.sort {
      svg {
        margin-bottom: 0;
      }
    }

    &.abbr {
      sup {
        position: relative;
        top: -0.4em;
        vertical-align: baseline;
      }
    }
  }

  .text {
    color: $gray-mid;
    font-weight: 600;

    a {
      background: $gray-light;
      border-bottom: 2px solid $gray-hover;
      color: $gray;
      text-decoration: none;

      &:hover {
        background: $gray-hover;
      }
    }
  }
}

.table-container {
  align-self: center;
  overflow-x: auto;
  width: 100%;
}

.table {
  border-radius: 3px;
  display: inline-grid;
  font-size: 12px;
  grid-template-rows: auto;
  min-width: 100%;

  .state-meta {
    grid-column: 1 / -1;
    left: 0;
    position: sticky;

    .state-meta-top {
      align-items: center;
      display: flex;
      flex-direction: row-reverse;
      flex-wrap: wrap;
      justify-content: space-between;
      margin-bottom: 1 + 0.25rem;
      margin-top: 1rem;

      svg {
        margin-right: 0.5rem;
      }

      .last-updated {
        align-items: center;
        color: $green;
        display: flex;
        flex-direction: row;
      }

      .state-page {
        align-items: center;
        background: $yellow-light;
        border-radius: 5px;
        color: $orange;
        cursor: pointer;
        display: flex;
        flex-direction: row;
        padding: 0.75rem;

        p {
          align-self: flex-end;
          flex: 1;
          margin: 0;
          text-align: right;
        }

        &:hover {
          background: $yellow-hover;
        }
      }
    }

    .state-meta-bottom {
      padding-bottom: 1.5rem;

      .disclaimer {
        background: $brick-light;
        border-radius: 5px;
        color: $brick;
        margin: auto;
        padding: 1rem 0.25rem;
        text-align: center;
        width: 16rem;

        svg {
          margin-right: 0.5rem;
        }
      }
    }
  }

  .row {
    cursor: pointer;
    display: contents;
    width: 100%;

    &:nth-child(odd) {
      .cell {
        background: $gray-light;
      }
    }

    &.is-highlighted {
      .cell {
        background: $gray-hover-opaque !important;
      }
    }

    &.heading {
      .cell {
        cursor: pointer;
        user-select: none;

        &:hover {
          background: $gray-hover-opaque !important;
        }
      }
    }

    &.is-total {
      position: sticky;
      top: 0;

      .cell {
        background: $gray-hover-opaque !important;
        padding-bottom: 0.75rem;
      }
    }

    .cell {
      align-items: center;
      border-radius: 3px;
      color: $gray;
      display: flex;
      flex-direction: row;
      margin-bottom: 0.25rem;
      padding: 0.5rem;
      position: relative;

      &:not(:first-child) {
        flex-basis: 4rem;
        flex-grow: 1;
        flex-shrink: 0;
        justify-content: flex-end;
      }

      &:first-child {
        background: $gray-hover-darker-opaque;
        flex-basis: 7rem;
        flex-grow: 0;
        flex-shrink: 0;
        justify-content: space-between;
        left: 0;
        position: sticky;
        z-index: 11;

        .sort-icon {
          left: unset;
          right: 0;
        }
      }

      svg {
        color: $gray-mid;
        margin-left: 0.2rem;
        stroke-width: 2.5px;
      }

      .sort-icon {
        left: 0;
        margin-left: 0.25rem;
        margin-right: 0.25rem;
        position: absolute;
        top: 0;

        &.is-confirmed {
          svg {
            color: $cherry;
          }
        }

        &.is-active {
          svg {
            color: $blue;
          }
        }

