          }
        }
      }

      &.is-confirmed {
        color: $cherry-mid;

        .is-highlighted {
          background: $cherry-light !important;

          &:hover {
            &:not(.disabled) {
              background: $cherry-hover !important;
            }
          }
        }
      }

      &.is-active {
        color: $blue-mid;

        .is-highlighted {
          background: $blue-light !important;

          &:hover {
            &:not(.disabled) {
              background: $blue-hover !important;
            }
          }
        }
      }

      &.is-recovered {
        color: $green-mid;

        .is-highlighted {
          background: $green-light !important;

          &:hover {
            &:not(.disabled) {
              background: $green-hover !important;
            }
          }
        }
