                fill: $cherry;
              }
            }
          }

          &.is-active {
            svg {
              fill: $blue-hover;
            }

            &.is-highlighted {
              svg {
                fill: $blue;
              }
            }
          }

          &.is-recovered {
            svg {
              fill: $green-hover;
            }

            &.is-highlighted {
              svg {
                fill: $green;
              }
            }
          }

          &.is-deceased {
            svg {
              fill: $gray-hover;
            }

            &.is-highlighted {
              svg {
                fill: $gray;
              }
            }
          }

