    bottom: 50%;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    left: 0;
    margin-left: auto;
    margin-right: auto;
    padding: 0.5rem;
    pointer-events: none;
    position: absolute;
    right: 0;
    transition: background, color 0.3s ease-in-out;
    width: 10rem;
    z-index: 99;

    &.is-confirmed {
      background: $cherry-light;
      color: $cherry;
    }

    &.is-active {
      background: $blue-light;
      color: $blue;
    }

    &.is-recovered {
      background: $green-light;
      color: $green;
    }

    &.is-deceased {
      background: $gray-light;
      color: $gray;
    }

    span {
      flex: 1;
    }

    svg {
      align-self: flex-start;
      margin-right: 0.5rem;
      stroke-width: 2px;
      width: 16px;
    }
  }

  .footnote {
    color: $gray;
    margin-bottom: 0;
    margin-top: 1em;
    text-align: left;
    z-index: 1;

    a {
      border-bottom: 2px solid $gray-hover;
      text-decoration: none;

