    &.is-recovered {
      .stem {
        stroke: $green-mid;
      }

      .trend-area {
        fill: $green-mid;
      }
    }

    &.is-deceased {
      .stem {
        stroke: $gray-mid;
      }

      .trend-area {
        fill: $gray-mid;
      }
    }

    &.is-tested {
