        text {
          color: $green-mid;
        }
      }
    }

    &.is-deceased {
      background: $gray-light;

      svg {
        .domain,
        .tick,
        line {
          stroke: $gray;
        }

        text {
          color: $gray-mid;
        }
      }
    }

    &.is-active {
      background: $blue-light;

      svg {
        .domain,
        .tick,
        line {
          stroke: $blue;
        }

        text {
          color: $blue-mid;
        }
      }
    }

    &.is-tested {
      background: $purple-light;

      svg {
        .domain,
        .tick,
        line {
          stroke: $purple;
        }

        text {
          color: $purple-mid;
        }
      }
    }

    &.is-vaccinated {
      background: $pink-light;

      svg {
        .domain,
        .tick,
        line {
          stroke: $pink;
        }

        text {
          color: $pink-mid;
        }
      }
    }

    &.is-tpr {
      background: $orange-light;

      svg {
        .domain,
        .tick,
        line {
          stroke: $orange;
        }

        text {
          color: $orange-mid;
        }
      }
    }

    &.is-brush {
      .overlay {
        fill: #6c757d40;
        pointer-events: all;
      }

      .selection {
        cursor: move;
        stroke: none;
      }

      svg {
        .domain,
        .tick,
        line {
          stroke: $gray;
        }

        text {
          color: $gray;
        }
      }
    }
  }
}

