.Volunteers {
  align-items: center;
  align-self: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 1.5rem;
  margin-left: 6rem;
  margin-top: 4rem;
  min-height: 30rem;
  width: calc(100% - 6rem);

  .wrapper {
    max-width: 80%;

    .description {
      background: $blue-light;
      border-radius: 0.25rem;
      color: $blue;
      font-size: 14px;
      line-height: 1.5;
      margin-bottom: 3rem;
      padding: 1rem;
      text-align: justify;
      text-justify: inter-word;
    }
  }

  .members {
    display: grid;
    grid-auto-rows: 6.5rem;
    grid-column-gap: 1%;
    grid-row-gap: 1rem;
    grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
    width: 80%;

    .last {
      display: flex;
      position: relative;

      .Member {
        background-color: $gray-light-opaque;
        box-sizing: border-box;
        height: 100%;
        position: absolute;
        width: 70%;

        &:hover {
          background-color: $gray-hover-opaque;
        }

        &.second {
          background-color: $white;
          left: 15%;

          &:hover {
            background-color: $gray-hover-opaque;
          }
        }

        &.third {
          left: 30%;
        }
      }
    }

    .Member {
      align-items: center;
      background-color: $gray-light;
      border-radius: 0.25rem;
      display: flex;
      flex-direction: row;
      padding: 1rem;
      position: relative;
      text-decoration: none;

      > a {
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
      }

      &:hover {
        background-color: $gray-hover;
      }

      .image {
        aspect-ratio: 1;
        border-radius: 10px;
        height: 100%;
        margin-right: 1rem;

        svg {
          border-radius: 10px;
          height: 100%;
          width: 100%;
        }
      }

      .link-external {
        align-self: flex-start;
        color: $gray;
        margin-left: 1rem;
      }

      .details {
        display: flex;
        flex-direction: column;
        height: 100%;
        min-width: 0;
        pointer-events: none;
        position: relative;
        white-space: nowrap;
        width: 100%;

        > * {
          overflow: hidden;
          text-overflow: ellipsis;
        }

        .name {
          color: #343a40ee;
          font-weight: 600;
          margin: 0;
          margin-bottom: 0.25rem;
        }

        .bio {
          color: $blue;
          font-size: 12px;
          margin: 0;
          margin-bottom: 0.25rem;
        }

        .socials {
          align-items: center;
          cursor: pointer;
          display: flex;
          line-height: 1.5rem;
          margin-top: auto;
          transition: opacity 0.15s ease-in-out;

          .icon {
            pointer-events: all;
          }

          svg {
            margin-right: 0.5rem;
          }

          .github {
            color: #6c757dbb;

            &:hover {
              color: $gray;
            }
          }

          .linkedin {
            color: $blue-mid;

            &:hover {
              color: $blue;
            }
          }

          .instagram {
            color: $pink-mid;

            &:hover {
              color: $pink;
            }
          }

          .twitter {
            color: $pblue-mid;

            &:hover {
              color: $pblue;
            }
          }
        }
      }
    }
  }
}

.About {
