    .Patients {
      width: 100%;
    }

    .day {
      justify-content: center;
      width: 100%;
    }
  }

  .Navbar {
    .expand {
      height: calc(100% - 4rem);
      overflow: auto;
      transform: none !important;
    }

    .expand-bottom {
      margin-top: auto;
    }

    .navbar-right {
      span {
        display: none;

        &:first-child {
          display: initial;
        }
      }
    }
  }

  .Search {
    .result-name {
      max-width: 10rem !important;
    }
  }

  .State {
    .MapExplorer,
    .StateHeader {
      width: calc(100% - 2rem);
    }

    .district-bar {
      width: calc(100% - 3rem);

      h1 {
        padding-left: 0;
      }
    }
  }

  .StateMeta {
    display: grid;
    grid-gap: 1rem;
    grid-template-rows: repeat(auto-fit, minmax(5rem));
    margin-bottom: 2.5rem;

    .meta-item {
      min-width: 1rem;
    }
  }

  .timeline {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }

  .LanguageSwitcher {
    margin-left: 0;
  }

  .VaccinationHeader {
    margin-bottom: 2rem;
    margin-top: 1rem;
  }

  .timeseries-header {
    .timeseries-options {
      flex-direction: column;

      .moving-average {
        margin-top: 0.75rem;
      }
    }
  }

  .MapSwitcher {
    .highlight,
    .clickable {
      height: 13rem;
    }
  }

  .Volunteers {
    margin-left: 0;

    .wrapper,
    .members {
      max-width: 100%;
      width: 100%;
    }
  }
}

// Dark Mode

.dark-mode {
  background: #161625 !important;
  color: #bdbdbd;

  .switch-wrapper > div {
    background: #161625 !important;
  }

  .is-tested {
    color: $dark-m-purple !important;

    svg {
      color: $dark-m-purple !important;
    }
  }

  .expand {
    background: #1e1e30 !important;
  }

  .Navbar,
  .expand {
    border-right: 0;

    .navbar-middle {
      color: #bdbdbd;
    }
  }

  .table {
    .cell {
      &:first-child {
        background: #1e1e30 !important;
      }

      &.heading {
        background: #1e1e30 !important;

        &:hover {
          background: #212130 !important;
        }
      }
    }

    .is-highlighted,
    .is-total {
      .cell {
        background: #1e1e30 !important;
      }
    }
  }

  .Search {
    input {
      background: #1e1e30 !important;
    }
  }

  .State {
    .header-right {
      h5 {
        color: #9673b9bb !important;
      }

      h2 {
        color: #9673b9 !important;
      }

      a {
        background: $dark-m-purple-light !important;
      }
    }

    .breadcrumb {
      ul {
        background: #1e1e30 !important;

        &::after {
          border-bottom-color: #1e1e30 !important;
        }

        li {
          a {
            color: $gray !important;

            &:hover {
              color: $white !important;
            }
          }
        }
      }
    }
  }

  .StateHeader {
    .header-right {
      color: $dark-m-purple-mid;

      h2 {
        color: $dark-m-purple;
      }

      a {
        background: $dark-m-purple-light;

        &:hover {
          background: $dark-m-purple-hover !important;
        }
      }
    }
  }

  .MapExplorer {
    #chart,
    #legend-choro {
      &.zone {
        filter: none;
      }

      &:not(.zone) {
        filter: invert(1) hue-rotate(180deg) saturate(1.5) url('#balance-color');
      }
    }

    .maplegend {
      background: #161625 !important;
    }

    .meta {
      h1 {
        &.tested {
          color: #9673b9 !important;
        }
      }

      h2 {
        &.tested {
          color: #9673b9 !important;
        }
      }
    }

    .panel {
      .panel-left {
        .tested {
          color: $dark-m-purple !important;
        }
      }
    }

    .stats {
      &.tested {
        background: $dark-m-purple-light !important;

        h1 {
          color: #9673b9 !important;
        }

        h5,
        h6 {
          color: #9673b9bb !important;
        }

        svg {
          stroke: #9673b9bb !important;

          &:hover {
            stroke: #9673b9 !important;
          }
        }
      }
    }

    .disclaimer {
      color: $gray-light-opaque;
    }
  }

  .Timeseries,
  .Minigraph {
    .stats {
      &.is-tested {
        h5 {
          color: $dark-m-purple-mid !important;

          &.title {
            color: $dark-m-purple-mid !important;
          }
        }

        h2,
        h6 {
          color: $dark-m-purple !important;
        }
      }
    }

    .svg-parent {
      &.is-tested {
        background: $dark-m-purple-light !important;

        svg {
          circle,
          .domain,
          .tick {
            stroke: $dark-m-purple !important;
          }

          circle {
            fill: $dark-m-purple !important;
          }

          .trend,
          line {
            stroke: #9673b999 !important;
          }

          .trend-area {
            fill: #9673b999 !important;
          }

          text {
            color: $dark-m-purple-mid !important;
            stroke: transparent;
          }
        }
      }

      &.is-brush {
        .handle--custom {
          stroke: $gray-light-opaque;
        }
      }
    }
  }

  .tpl {
    background: $dark-m-purple-light !important;

    h3,
    h5,
    svg,
    p {
      color: #9673b9bb !important;
    }

    h1 {
      color: #9673b9 !important;
    }
  }

  .tab {
    &.focused {
      background: #1e1e30 !important;
      color: #bdbdbd !important;
    }
  }

  .pills {
    button {
      &.selected {
        background: #{$yellow}50;
      }
    }
  }

  .TimeseriesExplorer {
    .state-selection {
      .dropdown {
        select {
          background: #1e1e30;
          background-image: linear-gradient(45deg, transparent 50%, $gray 50%),
          linear-gradient(135deg, $gray 50%, transparent 50%);
          background-position: calc(100% - 13px) 50%, calc(100% - 8px) 50%;
          background-repeat: no-repeat;
          background-size: 5px 5px, 5px 5px;
          border-color: #9494941a;
        }
      }
    }
  }

  input {
    &.switch {
      background-color: #161625 !important;
      border: 2px solid $gray-mid !important;

      &:checked {
        background-color: #6c757d99 !important;

        &::after {
          background-color: #cfcfcf !important;
        }
      }

      &::after {
        background-color: #cfcfcf !important;
      }
    }
  }

  .disabled {
    input {
      &.switch {
        background: $dark !important;
        border: $gray-light 2px solid !important;
        opacity: 0.7;

        &::after {
          background: $gray !important;
        }
      }
    }
  }

  .Volunteers {
    .members {
      .last {
        .Member {
          &:hover {
            background-color: #212130;
          }

          &.second {
            background-color: $dark-m-gray;

            &:hover {
              background-color: #212130;
            }
          }
        }
      }

      .Member {
        background-color: #1e1e30;

        &:hover {
          background-color: $gray-hover;
        }

        .details {
          .name {
            color: $gray;
          }
        }
      }
    }
  }

  .About {
    .question {
      color: $gray !important;
    }
  }

  .Demographics {
    .filters {
      background: #1e1e30 !important;
      box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.12), inset 0 -1px 0 0 #1e1e30;
    }

    .legend {
      background: #1e1e30 !important;
    }

    .react-date-picker {
      background: #1e1e30;
      border-color: #9494941a !important;
    }

    .modal-content {
      background: #1e1e30 !important;
    }

    .modal {
      background: #1e1e30bb !important;
    }
  }

  .StateDropdown {
    .dropdown {
      background: #1e1e30 !important;
    }
  }

  .StatisticDropdown {
    select {
      option {
        background: #1e1e30;
        color: $gray;
      }
    }
  }

  .TableLoader {
    svg {
      rect {
        fill: #1e1e30 !important;
      }
    }
  }
}

// Animation Support

.fadeInUp {
  animation-duration: 0.45s;
  animation-fill-mode: both;
  animation-name: fadeInUp;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }

  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.fadeInDown {
  animation-duration: 0.45s;
  animation-fill-mode: both;
  animation-name: fadeInDown;
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -20px, 0);
  }

  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.rotateDownRight {
  animation-duration: 0.25s;
  animation-fill-mode: both;
  animation-name: rotateDownRight;
}

@keyframes rotateDownRight {
  100% {
    transform: rotate(-90deg);
  }
}

.rotateRightDown {
  animation-duration: 0.25s;
  animation-fill-mode: both;
  animation-name: rotateRightDown;
}

@keyframes rotateRightDown {
  0% {
    transform: rotate(-90deg);
  }
}

.fadeOutDown {
  animation-duration: 0.45s;
  animation-fill-mode: both;
  animation-name: fadeOutDown;
}

@keyframes fadeOutDown {
  0% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }
}
